import "./App.css";

import { AiOutlineSearch } from "react-icons/ai";
import { useState } from "react";
import CalendarList from "./CalendarList";
import dummyCalendarList from "./dummy";

function App() {
  const [searchTerm, setSearchTerm] = useState("");

  return (
    <div className="App">
      <div className="title">
        <div className="sub-title">calendar</div>
        <div className="main-title">waffle</div>
      </div>

      <div className="search-bar">
        <input
          type="search"
          placeholder="  "
          onChange={(e) => { setSearchTerm(e.target.value); }}
        />
        <AiOutlineSearch className="logo" />
      </div>
      <CalendarList calendarList={dummyCalendarList} searchTerm={searchTerm}></CalendarList>
    </div>
  );
}

export default App;
